/* Fonts */
[class^=sb-icon-],
[class*=sb-icon-] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: "icomoon" !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  font-size: inherit;
  text-transform: none;
  line-height: inherit;
  /* Better Font Rendering */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.sb-icon-preview:before {
  content: "\e909";
}

.sb-icon-refresh:before {
  content: "\e90c";
}

.sb-icon-reviewer:before {
  content: "\e90a";
}

.sb-icon-folder:before {
  content: "\e902";
}

.sb-icon-comment:before {
  content: "\e908";
}

.sb-icon-send:before {
  content: "\e90b";
}

.sb-icon-book:before {
  content: "\e900";
}

.sb-icon-content:before {
  content: "\e901";
}

.sb-icon-collection:before {
  content: "\e903";
}

.sb-icon-doc:before {
  content: "\e904";
}

.sb-icon-mp3:before {
  content: "\e905";
}

.sb-icon-open-book:before {
  content: "\e906";
}

.sb-icon-video:before {
  content: "\e907";
}

.sb-lock-icon {
  cursor: pointer;
}

/*[class*="sb-icon-"] {
  -webkit-mask-size: contain;
  -webkit-mask-position: 50% 50%;
  -webkit-mask-repeat: no-repeat;
  mask-size: contain;
  mask-position: 50% 50%;
  mask-repeat: no-repeat;
  background-color: $primary-color;
  width: 16px;
  height: 16px;
  display: inline-block;
  cursor: pointer;
}
*/
[class*=sb-icon-primary] {
  background-color: var(--primary-color);
}

[class*=sb-icon-secondary] {
  background-color: var(--secondary-color);
}

[class*=sb-icon-warning] {
  background-color: var(--tertiary-color);
}

[class*=sb-icon-error] {
  background-color: var(--red);
}

.info-icon {
  background-color: var(--gray-400);
  -webkit-mask-size: contain;
  -webkit-mask-position: 50% 50%;
  -webkit-mask-repeat: no-repeat;
  mask-size: contain;
  mask-position: 50% 50%;
  mask-repeat: no-repeat;
  width: 1rem;
  height: 1rem;
  margin-left: 0.25rem;
  display: inline-block;
  vertical-align: bottom;
}

.icon-arrow-round-down {
  transform: rotate(90deg);
}

.icon-svg {
  position: relative;
  top: 0.0625rem;
  display: inline-block;
  font-style: normal;
  font-weight: 400;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  opacity: 1;
}
.icon-svg svg.icon {
  width: 100%;
  height: inherit;
}
.icon-svg--xxs {
  width: var(--icon-svg-xxs);
  height: var(--icon-svg-xxs);
}
.icon-svg--xs {
  width: var(--icon-svg-xs);
  height: var(--icon-svg-xs);
}
.icon-svg--sm {
  width: var(--icon-svg-sm);
  height: var(--icon-svg-sm);
}
.icon-svg--md {
  width: var(--icon-svg-md);
  height: var(--icon-svg-md);
}
.icon-svg--lg {
  width: var(--icon-svg-lg);
  height: var(--icon-svg-lg);
}
.icon-svg--xl {
  width: var(--icon-svg-xl);
  height: var(--icon-svg-xl);
}
.icon-svg--white {
  fill: var(--white);
}
.icon-svg--primary {
  fill: var(--primary-color);
}
.icon-svg--secondary {
  fill: var(--secondary-color);
}
.icon-svg--tertiary {
  fill: var(--tertiary-color);
}
.icon-svg--red {
  fill: var(--red-400);
}
.icon-svg--gray {
  fill: var(--gray-800);
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-family: inherit;
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -ms-overflow-style: scrollbar;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

@-ms-viewport {
  width: device-width;
}
article,
aside,
dialog,
figcaption,
figure,
footer,
header,
hgroup,
main,
nav,
section {
  display: block;
}

body {
  margin: 0;
  font-family: var(--font-stack-en);
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: var(--gray-800);
  text-align: left;
  background: #E5EDF5;
}

[tabindex="-1"]:focus {
  outline: none !important;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 0.5rem;
}

p {
  margin-top: 0;
  margin-bottom: 1rem;
}

abbr[title],
abbr[data-original-title] {
  text-decoration: underline;
  text-decoration: underline dotted;
  cursor: help;
  border-bottom: 0;
}

address {
  margin-bottom: 1rem;
  font-style: normal;
  line-height: inherit;
}

ol,
ul,
dl {
  margin-top: 0;
  margin-bottom: 1rem;
}

ol ol,
ul ul,
ol ul,
ul ol {
  margin-bottom: 0;
}

dt {
  font-weight: 700;
}

dd {
  margin-bottom: 0.5rem;
  margin-left: 0;
}

blockquote {
  margin: 0 0 1rem;
}

dfn {
  font-style: italic;
}

b,
strong {
  font-weight: bolder;
}

small {
  font-size: 80%;
}

sub,
sup {
  position: relative;
  font-size: 75%;
  line-height: 0;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

a {
  color: theme-color("primary");
  text-decoration: none;
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
}

a:hover {
  color: #0056b3;
  text-decoration: underline;
}

a:not([href]):not([tabindex]) {
  color: inherit;
  text-decoration: none;
}

a:not([href]):not([tabindex]):hover,
a:not([href]):not([tabindex]):focus {
  color: inherit;
  text-decoration: none;
}

a:not([href]):not([tabindex]):focus {
  outline: 0;
}

pre,
code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

pre {
  margin-top: 0;
  margin-bottom: 1rem;
  overflow: auto;
  -ms-overflow-style: scrollbar;
}

figure {
  margin: 0 0 1rem;
}

img {
  vertical-align: middle;
  border-style: none;
}

svg:not(:root) {
  overflow: hidden;
}

a,
area,
button,
[role=button],
input:not([type=range]),
label,
select,
summary,
textarea {
  touch-action: manipulation;
}

table {
  border-collapse: collapse;
}

caption {
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  color: #868e96;
  text-align: left;
  caption-side: bottom;
}

th {
  text-align: inherit;
}

label {
  display: inline-block;
  margin-bottom: 0.5rem;
}

button {
  border-radius: 0;
}

button:focus {
  outline: 0px dotted;
  outline: 0px auto transparent;
}

input,
button,
select,
optgroup,
textarea {
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
html [type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

input[type=radio],
input[type=checkbox] {
  box-sizing: border-box;
  padding: 0;
}

input[type=date],
input[type=time],
input[type=datetime-local],
input[type=month] {
  -webkit-appearance: listbox;
}

textarea {
  overflow: auto;
  resize: vertical;
}

fieldset {
  min-width: 0;
  padding: 0;
  margin: 0;
  border: 0;
}

legend {
  display: block;
  width: 100%;
  max-width: 100%;
  padding: 0;
  margin-bottom: 0.5rem;
  font-size: 1.5rem;
  line-height: inherit;
  color: inherit;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  outline-offset: -2px;
  -webkit-appearance: none;
}

[type=search]::-webkit-search-cancel-button,
[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  font: inherit;
  -webkit-appearance: button;
}

output {
  display: inline-block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none !important;
}

